export default {
    asyncData(context) {
        if(context.payload){
            return {
                loadedPost: context.payload.postData
            }
        }
        return context.app.$axios.$get('posts/' + context.params.id + '.json')
        .then(data => {
            return {
                loadedPost: data
            }
        })
        .catch((e) => context.error(e))
    }
}
